.registration {
    margin-bottom: 42px;
    padding-top: 40px;
    align-items: center;
    @media screen and (min-width: $tablet) and (max-width: $pre-desctop) {
        padding-top: 0px;
        padding-bottom: 68px;
    }
    @media screen and (min-width: $desctop) {
        padding-top: 0px;
        padding-bottom: 126px;
    }
}

.title__registration {
    margin-bottom: 27px;
    align-items: center;
    @media screen and (min-width: $tablet) and (max-width: $pre-desctop) {
        font-size: 32px;
        line-height: 1.22;
        color: #000;
        font-weight: 500;
    }
    @media screen and (min-width: $desctop) {
        color: var(--main-color);
        font-size: 32px;
        font-weight: 500;
        line-height: 1.22;
    }
}

.registration__form {
    width: 280px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 10px;
    @media screen and (max-width: $pre-tablet) {
        background-image: linear-gradient(rgba(162, 143, 143, 0.524), rgba(162, 143, 143, 0.524)), url(../images/img-register.png);
    }
   
    @media screen and (min-width: $tablet) and (max-width: $pre-desctop) {
        width: 704px;
        display: flex;
        background-image: url(../images/small-donuts.png), url(../images/small-cloud.png), url(../images/strawberry-donuts.png);
        background-repeat: no-repeat;
        background-position: 25px 170px, left, right;
        position: relative;
    }
    @media screen and (min-width: $desctop) {
        width: 1034px;
        height: 246px;
        display: flex;
        background-image: url(../images/big-donuts.png), url(../images/big-cloud.png), url(../images/strawberry-donuts.png);
        background-repeat: no-repeat;
        background-position: 15px 97px, left, right;
        position: relative;
    }

}
.date {
    display: flex;
    border-bottom: 1px solid var(--border-color-white);
    justify-content: center;
    margin-left: 8px;
    margin-right: 8px;
    @media screen and (min-width: $tablet) and (max-width: $pre-desctop) {
        max-width: 90px;
        flex-shrink: 0;
        border: none;
        margin-left: 62px;
        margin-right: 0px;
        border-bottom: 0;
       
        &::after {
                position: absolute;
                width: 1px;
                height: 154px;
                color: rgba(0, 0, 0, 0.5);
                content: " ";
                border: 1px solid rgba(0, 0, 0, 0.5);
                top: 60px;
                left: 184px;
            }
    }
    @media screen and (min-width: $desctop) {
        width: 127px;
        border-bottom: 0; 
        margin-left: 213px;
        margin-top: auto;
        margin-bottom: auto;
        &::after {
                position: absolute;
                width: 1px;
                height: 184px;
                color: rgba(0, 0, 0, 0.5);
                content: " ";
                border: 1px solid rgba(0, 0, 0, 0.5);
                top: 40px;
                left: 368px;
            }
    }
}
.date__text {
    color: #fff;
    font-weight: 500;
    font-size: 18px;
    line-height: 1.22;
    text-align: center;
    padding-top: 40px;
    padding-bottom: 20px;
    padding-right: 0px;
    @media screen and (min-width: $tablet) and (max-width: $pre-desctop) {
        color: #000;
        // position: relative;
        font-size: 18px;
        line-height: 1.11;
        text-align: right;
        vertical-align: center;
        padding-right: 0px;
        padding-top: 0px;
        padding-bottom: 0px;
        margin-top: 85px;
       
    }
    @media screen and (min-width: $desctop) {
        color: var(--main-color);
        font-size: 20px;
        line-height: 1.2;
        align-items: right;
        vertical-align: top;
    }
}
