.registration__info {
    margin-left: 8px;
    margin-bottom: 24px;
    @media screen and (min-width: $tablet) and (max-width: $pre-desctop) {
        margin-left: 56px;
        margin-top: 40px;
        margin-bottom: 0px;
    }
    @media screen and (min-width: $desctop) {
        margin-top: 40px;
        margin-bottom: 0px;
        margin-left: 56px;
    }
}
.registration__text {
    color: #fff;
    margin-bottom: 20px;
    @media screen and (min-width: $tablet) and (max-width: $pre-desctop) {
        color: #000;
        font-weight: 400;
        font-size: 16px;
        line-height: 1.25;
        text-align: left;
        vertical-align: top;
    }
    @media screen and (min-width: $desctop) {
        color: #000;
        width: 252px;
        font-weight: 400;
        font-size: 20px;
        line-height: 1.25;
        text-align: left;
        vertical-align: top;
        }
}

.registration__input {
    width: 264px;
    display: block;
    margin-bottom: 20px;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    border-bottom: 1px solid var(--border-color-white);
    background-color: transparent;
    align-items: center;
    color: #fff;

    &::placeholder {
        color: #fff;
    }
    @media screen and (min-width: $tablet) and (max-width: $pre-desctop) {
        width: 237px;
        padding-left: 0px;
        border-color: var(--border-color-black);
        color: rgba(0, 0, 0, 0.5);
        margin-bottom: 16px;
        &::placeholder {
            color: rgba(0, 0, 0, 0.5);
        }
    }
    @media screen and (min-width: $desctop) {
        // padding-left: 24px;
        width: 212px;
        border-color: var(--border-color-black);
        color: rgba(0, 0, 0, 0.5);
        margin-bottom: 16px;
        
        &::placeholder {
            color: rgba(0, 0, 0, 0.5);
        }
    }
}
.form {
    margin-top: 24px;
    @media screen and (min-width: $tablet) {
        display: flex; 
        margin-top: 0px;
        height: 246px;
    }
    @media screen and (min-width: $desctop) {
        margin-top: 0px;
    }
}
.register {
    padding-bottom: 32px;
    
    @media screen and (min-width: $tablet) and (max-width: $pre-desctop) {
        margin: 103px 40px 103px 74px;
    }
    @media screen and (min-width: $desctop) {
        margin-top: 103px;
        margin-right: 232px;
        margin-bottom: 103px;
    }

}
.registration__button {
    @extend .register-link;
    display: block;
    border: 0px solid;
    // margin-bottom: 32px;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;

    &:hover,
    &:focus {
        background-color: #fff;
        border: 1px solid var(--pink-color);
        filter: drop-shadow(10px 15px 25px rgba(232, 183, 183, 0.45));
        color: rgba(243, 132, 140, 1);
    }
}